import request from "./request";

// 
export function sendComment(type, id, content, cookie) {
  return request({
    url: "/comment",
    method: "get",
    params: {
      t: 1, // 
      type, // 
      id,
      content,
      cookie,
      timestamp: new Date().getTime(),
    },
  });
}

// 
/* cid :  id
t :  ,1  ,0  */
export function sendLikeComment(id, cid, t, type, cookie) {
  return request({
    url: "/comment/like",
    params: {
      id,
      cid,
      t,
      type,
      cookie,
    },
  });
}
