import React, { useState } from "react";
import propTypes from "prop-types";
import { getParseLoginState, getMatchReg } from "@/utils/format-utils";
import { Form, Input, Button, Checkbox, message } from "antd";
import loginFormStyle from "./style.module.css";
import { useDispatch } from "react-redux";
import { getLoginProfileInfo } from "../theme-login/store/actionCreator";
import { sendRegisterCode, sendRegister } from "../../services/login";

const layout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 16 },
};
const tailLayout = {
  wrapperCol: { span: 30 },
};

/**
 * 
 */
const ThemeLoginForm = (props) => {
  // prop/state
  // ""
  const { loginState } = props;
  const [phone, setPhone] = useState(null);
  const [isSendSatte, setIsSendSatte] = useState(false);
  const [second, setSecond] = useState(60);
  // : phone->''  email->''  register -> 
  const parseLoginModeText = getParseLoginState(loginState);
  // : ,
  const mathchReg = getMatchReg(loginState);
  const mathchPhoneReg = getMatchReg("phone");
  const pwdReg = /[0-9a-zA-Z._-]{6,20}/;
  const codeReg = /[0-9a-zA-Z._-]{4,20}/;
  // console.log(loginState, parseLoginModeText, `--->${mathchReg}`)
  // redux hook
  const dispatch = useDispatch();

  // other handle

  // component handle
  const onFinish = ({ username, password }) => {
    // : 
    dispatch(getLoginProfileInfo(username, password, true));
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  // 
  const onRegisterFinish = (value) => {
    const { phone, password, code, nickname } = value;
    sendRegister(code, phone, password, nickname).then((res) => {
      console.log(res);
      if (res.code === 200) message.success("");
      else message.warn(res.message);
    });
  };
  const onRegisterFinishFailed = () => {};

  // handle function
  const handleSendCode = () => {
    // 60
    if (!isSendSatte) {
      let i = 0;
      const timer = setInterval(() => {
        i++;
        setSecond(second - i);
        if (i >= 60) {
          clearInterval(timer);
          setIsSendSatte(false);
          setSecond(60);
        }
      }, 1000);
      // 
      !isSendSatte &&
        sendRegisterCode(phone).then((res) => {
          if (res.code === 200) message.success("");
          else message.warn(", 60");
        });
    }
    setIsSendSatte(true);
  };

  return (
    <>
      <Form
        style={{
          display: loginState !== "register" ? "block" : "none",
        }}
        {...layout}
        name="basic"
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Form.Item
          label={parseLoginModeText}
          name="username"
          rules={[
            {
              pattern: mathchReg,
              message: `${parseLoginModeText}`,
            },
            { required: true, message: "" },
          ]}
        >
          <Input autoFocus />
        </Form.Item>

        <Form.Item
          label=""
          name="password"
          rules={[
            { pattern: pwdReg, message: "6" },
            { required: true, message: "!" },
          ]}
        >
          <Input.Password />
        </Form.Item>
        <div className={loginFormStyle.textAlignRight}>
          <Checkbox className={loginFormStyle.mr80} defaultChecked={true}>
            
          </Checkbox>
          <span className={loginFormStyle.forgetPwd}>?</span>
        </div>
        <Form.Item {...tailLayout}>
          <Button
            type="primary"
            htmlType="submit"
            size="middle"
            block
            shape="round"
          >
            
          </Button>
        </Form.Item>
      </Form>
      <Form
        style={{
          display: loginState === "register" ? "block" : "none",
        }}
        {...layout}
        name="basic"
        onFinish={onRegisterFinish}
        onFinishFailed={onRegisterFinishFailed}
      >
        <Form.Item
          label={parseLoginModeText}
          name="phone"
          rules={[
            {
              pattern: mathchPhoneReg,
              message: ``,
            },
            { required: true, message: "" },
          ]}
        >
          <Input
            autoFocus
            value={phone}
            onChange={({ target }) => {
              setPhone(target.value);
            }}
          />
        </Form.Item>

        <Form.Item
          label=""
          name="password"
          rules={[{ pattern: pwdReg, message: "6", required: true }]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item>
          <div
            className={loginFormStyle.register}
            onClick={() => handleSendCode()}
          >
            {isSendSatte ? second + "s" : ""}
          </div>
        </Form.Item>
        <Form.Item
          className={loginFormStyle.gap}
          label=""
          name="code"
          rules={[
            { pattern: codeReg, message: "4" },
            { required: true, message: "" },
          ]}
        >
          <Input disabled={!isSendSatte} />
        </Form.Item>
        <Form.Item
          className={loginFormStyle.gap}
          label=""
          name="nickname"
          rules={[{ required: true, message: "" }]}
        >
          <Input disabled={!isSendSatte} />
        </Form.Item>
        <Form.Item {...tailLayout}>
          <Button
            type="primary"
            htmlType="submit"
            size="middle"
            block
            shape="round"
          >
            
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};

ThemeLoginForm.propTypes = {
  loginState: propTypes.string,
};

ThemeLoginForm.defaultProps = {
  loginState: "phone",
};

export default ThemeLoginForm;
