import request from "./request";
// 
export const getUserSongList = (userId) => {
  return request({
    url: "/user/playlist",
    params: {
      uid: userId,
      timestamp: new Date().getTime(),
    },
  });
};

// 
export const setCreateUserSongList = (name, cookie) => {
  return request({
    url: "/playlist/create",
    params: {
      name,
      cookie,
    },
  });
};

// 
export const getUserInfo = (cookie) => {
  return request({
    url: "/user/subcount",
    params: {
      cookie,
    },
  });
};

// 
export const getUserDetailInfo = (uid) => {
  return request({
    url: "/user/detail",
    params: {
      uid,
    },
  });
};

// 
export const getUserRecordInfo = (type, uid) => {
  return request({
    url: "/user/record",
    params: {
      type,
      uid,
    },
  });
};

//  ()
export const getUserPlayListInfo = (id) => {
  return request({
    url: "/playlist/detail",
    params: {
      id,
    },
  });
};
