import request from './request'

// 
export function getSongList(limit,offset=0) {
  return request({
    url:'/top/playlist',
    params: {
      limit,
      offset
    }
  })
}

export function getSongCategory() {
  return request({
    url: "/playlist/catlist"
  })
}

export function getSongCategoryList(cat="", offset=0, limit = 35) {
  return request({
    url: "/top/playlist",
    params: {
      cat,
      limit,
      offset
    }
  })
}

// 
/* cid :  id
t :  ,1  ,0  */
export function sendLikeComment(id, cid, t, cookie) {
  return request({
    url: "/comment/like",
    params: {
      id,
      cid,
      t,
      type: 0,
      cookie
    }
  })
}

/* , id */
export function sendCollectSonglist(id, cookie) {
  return request({
    url: "/playlist/subscribe",
    params: {
      t: 1,
      id,
      cookie
    }
  })
}


// 
export function getSongDetailData(id) {
  return request({
    url: '/playlist/detail',
    params: {
      id
    }
  })
}