import request from "./request";
export function getTopBanners() {
  return request({
    url: "/banner",
  });
}

export function getHotRecommends(limit) {
  return request({
    url: "/personalized",
    params: {
      limit,
    },
  });
}

export function getNewAlbums(offset, limit, area, type, year, month) {
  return request({
    url: "/top/album",
    params: {
      offset,
      limit,
      area,
      type,
      year,
      month,
    },
  });
}

export function getTopLists(id) {
  return request({
    url: "/playlist/detail",
    params: {
      id,
    },
  });
}

// 
export function getSettleSinger(limit) {
  return request({
    url: "/artist/list",
    params: {
      limit,
    },
  });
}

// 
export function getHotAnchor(limit) {
  return request({
    url: "/dj/toplist",
    params: {
      limit,
    },
  });
}

// 
export function getDayRecommend(cookie) {
  return request({
    url: "/recommend/songs",
    params: {
      cookie,
    },
  });
}

// 
export function getPersonRecommend(cookie) {
  return request({
    url: "/recommend/resource",
    method: "get",
    params: {
      cookie: cookie,
    },
  });
}
