import request from "./request";

// 
export function getAlbumDetail(id) {
  return request({
    url: "/album",
    params: {
      id,
    },
  });
}

// 
export function getAlbumComment(id, limit, offset, before) {
  return request({
    url: "/comment/album",
    params: {
      id,
      limit,
      offset,
      before,
    },
  });
}
